.switch {
  display: none;
  width: 35px;
  height: 35px;
  position: relative;
  overflow: hidden;
}
@media screen and (max-width: 767px) {
  .switch {
    display: block;
  }
}
.switch__line {
  position: absolute;
  height: 4px;
  border-radius: 5px;
  width: 100%;
  left: 0;
  background-color: #fff;
}
.switch__line:nth-child(1) {
  top: 5px;
  transition: 0.3s;
}
.switch__line:nth-child(2) {
  top: calc(50% - 2px);
  transition: 0.3s;
}
.switch__line:nth-child(3) {
  bottom: 5px;
  transition: 0.3s;
}

.switch:hover {
  cursor: pointer;
}
.switch:hover .switch__line:nth-child(1) {
  top: 7px;
}
.switch:hover .switch__line:nth-child(3) {
  bottom: 7px;
}

.switch.open {
  cursor: pointer;
}
.switch.open .switch__line:nth-child(1) {
  top: calc(50% - 2px);
  transform: rotate(45deg) translate(0px, 0px);
}
.switch.open .switch__line:nth-child(2) {
  top: calc(50% - 2px);
  transform: rotate(-45deg) translate(0px, 0px);
}
.switch.open .switch__line:nth-child(3) {
  bottom: -4px;
}

