  background-color: rgba(61, 64, 80, 0.85);
  border-radius: 10px;
  border: 4px solid #10101d;
}

.sbOptions::-webkit-scrollbar-thumb {
  background-color: #3E9CDC;
  border-radius: 10px;
  border: 4px solid #10101d;
}

.range {
  width: 774px;
}
.range .range__slider {
  -webkit-appearance: none;
  width: 100%;
  height: 6px;
  border-radius: 5px;
  background: #d3d3d3;
  outline: none;
  opacity: 0.7;
  -webkit-transition: 0.2s;
  transition: opacity 0.2s;
}
.range .range__slider:hover {
  opacity: 1;
}
.range .range__slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background: #3E9CDC;
  border: 2px solid #286690;
  cursor: pointer;
}
.range .range__slider::-moz-range-thumb {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background: #4CAF50;
  cursor: pointer;
}
.range__title {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  color: #fff;
  font-size: 18px;
  margin-bottom: 11px;
}
@media screen and (max-width: 767px) {
  .range__title {
    font-size: 13px;
  }
}